import ReactGA from 'react-ga';
import React, { useEffect } from 'react';
import { useLocation } from 'react-router-dom'
import { Twitter } from '@icons-pack/react-simple-icons';
import { Instagram } from '@icons-pack/react-simple-icons';
import { Facebook } from '@icons-pack/react-simple-icons';

function TwitterLogo() {
  //return <Twitter color="#1DA1F2" size={24} />;
  return <Twitter color="#E8D11F" size={30} />;
}

function InstagramLogo() {
  //return <Instagram color="#E4405F" size={24} />;
  return <Instagram color="#E8D115" size={30} />;
}

function FacebookLogo() {
  //return <Facebook color="#1877F2" size={24} />;
  return <Facebook color="#E8D11F" size={30} />;
}

function usePageViews() {
  let location = useLocation()

  useEffect(
    () => {
      ReactGA.send(['pageview', location.pathname])
    },
    [location]
  )
}

function Footer() {
  usePageViews()
  return (
  <div className="footer">
    <a href="https://twitter.com/logopikto"><TwitterLogo /></a>
    <a href="https://instagram.com/logopikto"><InstagramLogo /></a>
    <a href="https://facebook.com/logopikto"><FacebookLogo /></a>
  </div>
  );
}

export default Footer
